// ==========  ==========

// 
export type ImageFormat =
  | 'jpeg'
  | 'png'
  | 'webp'
  | 'gif'
  | 'bmp'
  | 'ico'
  | 'icns'
  | 'avif'
  | 'tiff'
  | 'heif'
  | 'heic'
  | 'jxl'
  | 'pdf'
  | 'svg'
  | 'jp2'
  | 'tga';

// 
export type ConvertStatus = 'pending' | 'converting' | 'completed' | 'failed';

// 
export interface ImageFile {
  id: string;
  file: File;
  name: string;
  size: number;
  width: number;
  height: number;
  format: string;
  previewUrl: string;
  status: ConvertStatus;
  progress: number;
  convertedBlob?: Blob;
  convertedSize?: number;
  convertedUrl?: string;
  error?: string;
  originalUrl?: string; // URL
}

// 
export interface ConvertOptions {
  targetFormat: ImageFormat;
  quality: number; // 0-100,  jpeg/webp/avif
  resize?: {
    width: number;
    height: number;
    maintainAspectRatio: boolean;
  };
  rotate?: number; //  (0, 90, 180, 270)
  flip?: boolean; // 
  flop?: boolean; // 
  crop?: {
    x: number;
    y: number;
    width: number;
    height: number;
  };
  adjustment?: {
    brightness: number; // -100  1000 
    contrast: number; // -100  1000 
    saturation: number; // -100  1000 
  };
  filter?: {
    blur: number; // 0-100
    sharpen: number; // 0-100
    grayscale: boolean;
    sepia: boolean;
    invert: boolean;
  };
  watermark?: {
    text: string;
    color: string;
    fontSize: number;
    position: 'top-left' | 'top-right' | 'bottom-left' | 'bottom-right' | 'center';
    opacity: number;
    // PDF 
    type?: 'corner' | 'tiled' | 'diagonal' | 'full';
    rotation?: number; // 
    spacing?: number; // 
    margin?: number; // 
    // 
    fontScale?: number; // 
  };
  imageWatermark?: {
    imageData: string;
    width: number;
    height: number;
    position: 'top-left' | 'top-right' | 'bottom-left' | 'bottom-right' | 'center';
    opacity: number;
    scale: number;
    // PDF 
    type?: 'corner' | 'tiled' | 'diagonal' | 'full';
    rotation?: number; // 
    spacing?: number; // 
    margin?: number; // 
    // 
    imageScale?: number; // 
  };
  backgroundColor?: string;
  iconSizes?: number[];
}

// 
export interface AdvancedSettings {
  quality: number;
  fileNamePrefix: string;
  fileNameSuffix: string;

  enableResize: boolean;
  resizeWidth: number;
  resizeHeight: number;
  maintainAspectRatio: boolean;

  rotate: number;
  flip: boolean;
  flop: boolean;

  enableCrop: boolean;
  cropX: number;
  cropY: number;
  cropWidth: number;
  cropHeight: number;

  enableAdjustment: boolean;
  brightness: number;
  contrast: number;
  saturation: number;

  enableFilter: boolean;
  blur: number;
  sharpen: number;
  grayscale: boolean;
  sepia: boolean;
  invert: boolean;

  enableWatermark: boolean;
  watermarkType: 'text' | 'image';
  watermarkText: string;
  watermarkColor: string;
  watermarkFontSize: number;
  watermarkPosition: 'top-left' | 'top-right' | 'bottom-left' | 'bottom-right' | 'center';
  watermarkOpacity: number;
  // PDF 
  watermarkStyleType: 'corner' | 'tiled' | 'diagonal' | 'full';
  watermarkRotation: number; // 
  watermarkSpacing: number; // 
  watermarkMargin: number; // 
  watermarkFontScale: number; // 

  watermarkImage: string;
  watermarkImageWidth: number;
  watermarkImageHeight: number;
  watermarkImageScale: number;
  watermarkImagePosition: 'top-left' | 'top-right' | 'bottom-left' | 'bottom-right' | 'center';
  watermarkImageOpacity: number;
  // PDF 
  watermarkImageStyleType: 'corner' | 'tiled' | 'diagonal' | 'full';
  watermarkImageRotation: number; // 
  watermarkImageSpacing: number; // 
  watermarkImageMargin: number; // 
  watermarkImageImageScale: number; // 

  backgroundColor: string;

  iconSizes: number[];

  // 
  enableCompression: boolean;
  compressionLevel: number; // 0-9

  // 
  enableDenoise: boolean;
  denoiseLevel: number;
}

